.card{
	border: 1px solid #7777;
	border-radius: 15px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 320px;
	margin: 25px 8px 0px 0px;
}
.card__image {
	width: 100%;
	height: 250px;
	background-color: #ccc;
	margin: 10px 0;
}