.wrap {
  text-align: center;
  max-width: 90%;
  margin: 0 auto;
}
.wrapper {
	text-align: center;
	max-width: 1920px;
	padding: 0 calc(10% - 12px);
	min-height: 80vh;
	margin: 0 auto;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	align-items: start;
 }
