.cardWrap {
	border: 1px solid rgba(119, 119, 119, 0.344);
	width: 360px;
	padding: 5px;
	margin: 0px 10px 25px 0;
	border-radius: 5px;
}
.last {
	margin-bottom: 30px;
}
.desctiption {
	height: 48px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-top: 7px;
}
@media (max-width: 414px){
	.cardWrap {
		margin: 0 0 25px 0;
	}
}
.cardWrap img {
	margin: 15px 0px 0px 0px;
}
.cardWrap p::first-letter {
	text-transform: uppercase;
}
/* .cardWrap p {
	text-transform: lowercase;
} */
h1 {
	text-align: center;
	margin: 10px 0 55px; 
}
.card__text {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 10px; 
}
.card__text a p {
	color: #444444;
}
.card__text img {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	margin: 10px 10px 0px 0px;
	object-fit: cover;
	object-position: center;
}


.cardBtns {
	display: flex;
	justify-content: space-between;
	width: 77px;
	height: 30px;
}
.footer {
	text-align: center;
	background-color: #14213d;
	color: #fff;
	padding: 18px 0;

}
.loading {
	text-align: center;
	background-color: #fff;
	color: #000000;
}
.heartLike {
	transition: all ease-in .3s !important;
	color: #fff !important;
	background-color: red !important;
	border-color: red !important;
}


