@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	text-decoration: none;
	font-family: 'Poppins', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrap {
  text-align: center;
  max-width: 90%;
  margin: 0 auto;
}
.wrapper {
	text-align: center;
	max-width: 1920px;
	padding: 0 calc(10% - 12px);
	min-height: 80vh;
	margin: 0 auto;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
	align-items: start;
 }
/* .cards {
	display: flex;
	justify-content: space-around;
	align-items: start;
	flex-wrap: wrap;
	width: 100%;
	margin: 0 auto 100px;
} */
.cardWrap {
	border: 1px solid rgba(119, 119, 119, 0.344);
	width: 360px;
	padding: 5px;
	margin: 0px 10px 25px 0;
	border-radius: 5px;
}
.last {
	margin-bottom: 30px;
}
.desctiption {
	height: 48px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding-top: 7px;
}
@media (max-width: 414px){
	.cardWrap {
		margin: 0 0 25px 0;
	}
}
.cardWrap img {
	margin: 15px 0px 0px 0px;
}
.cardWrap p::first-letter {
	text-transform: uppercase;
}
/* .cardWrap p {
	text-transform: lowercase;
} */
h1 {
	text-align: center;
	margin: 10px 0 55px; 
}
.card__text {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 10px; 
}
.card__text a p {
	color: #444444;
}
.card__text img {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	margin: 10px 10px 0px 0px;
	object-fit: cover;
	object-position: center;
}


.cardBtns {
	display: flex;
	justify-content: space-between;
	width: 77px;
	height: 30px;
}
.footer {
	text-align: center;
	background-color: #14213d;
	color: #fff;
	padding: 18px 0;

}
.loading {
	text-align: center;
	background-color: #fff;
	color: #000000;
}
.heartLike {
	transition: all ease-in .3s !important;
	color: #fff !important;
	background-color: red !important;
	border-color: red !important;
}



.card{
	border: 1px solid #7777;
	border-radius: 15px;
	padding: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	width: 320px;
	margin: 25px 8px 0px 0px;
}
.card__image {
	width: 100%;
	height: 250px;
	background-color: #ccc;
	margin: 10px 0;
}
.nav {
	padding: 15px 30px;
	background-color: #ffe1d1af;
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	margin: 0px 0px 50px 0px;
	
}
.nav__list {
	display: flex;
	width: 400px;
	justify-content: space-between;
	align-items: center;
	list-style: none;
}
.navlink a {
	color: #000;
	font-size: 18px;
}
.nav__logo {
	font-size: 22px;
	color: #000;
	margin: 0px 15px 0px 0px;
}
