.nav {
	padding: 15px 30px;
	background-color: #ffe1d1af;
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	margin: 0px 0px 50px 0px;
	
}
.nav__list {
	display: flex;
	width: 400px;
	justify-content: space-between;
	align-items: center;
	list-style: none;
}
.navlink a {
	color: #000;
	font-size: 18px;
}
.nav__logo {
	font-size: 22px;
	color: #000;
	margin: 0px 15px 0px 0px;
}